import React from 'react'
import dropIcon from '../../assets/img/Vector2.png'
import Counter from '../Counter/Counter';
import DropArea from '../DropArea/DropArea';
import TextInput from '../TextInput/TextInput'
const { useState, useEffect } = React;


const ListItems = (props) => {
    const { overrideStyle ,defaultValue, data} = props
  const [isOpen, setOpen] = useState(false);
  const [items, setItem] = useState(data);
  const [selectedItem, setSelectedItem] = useState(null);
  
  const toggleDropdown = () => setOpen(!isOpen);
  
  const handleItemClick = (id) => {
    selectedItem == id ? setSelectedItem(null) : setSelectedItem(id);
  }
  
  return (
    <div className='dropdown' style={overrideStyle}>
      <div className='dropdown-header' onClick={toggleDropdown}>
        {selectedItem ? items.find(item => item.id == selectedItem).label : defaultValue}
        <img src={dropIcon} style={{ width: '15px' }} />
      </div>
      <div className={`dropdown-body ${isOpen && 'open-list-item'}`} style={{backgroundColor:'#FFF0FC',justifyContent:'start'}}>
  <div className='d-flex flex-wrap justify-content-start'>
    <div style={{width:'65%'}}>
    <div className='d-flex justify-content-start flex-wrap' style={{marginLeft:'10px'}}>
    <TextInput title={'Name'} overrideStyle={{minWidth:'150px',width:'40%',height:'30px',marginTop:'15px',marginBottom:'15px',}}/>
    <TextInput title={'Short description'} overrideStyle={{minWidth:'300px',maxWidth:'384px',width:'100%',marginTop:'15px'}}/>
 
    </div>
    <div className='d-flex justify-content-start flex-wrap' style={{marginLeft:'10px'}}>
    <TextInput type='number' title={'Weight'} overrideStyle={{marginTop:'15px',marginBottom:'15px',minWidth:'150px',width:'25%',height:'30px'}}/>
    <TextInput  type='number' title={'Height'} overrideStyle={{marginBottom:'15px',marginTop:'15px',minWidth:'150px',width:'25%',height:'30px'}}/>
    <TextInput  type='number' title={'Width'} overrideStyle={{marginBottom:'15px',marginTop:'15px',minWidth:'150px',width:'25%',height:'30px'}}/>
    <TextInput  type='number' title={'Length'} overrideStyle={{marginBottom:'15px',marginTop:'15px',minWidth:'150px',width:'25%',height:'30px'}}/>
    </div>
    <div className='d-flex justify-content-start flex-wrap' style={{marginLeft:'10px',marginTop:'30px'}}>
    <TextInput multiline title={'Special Notes'} overrideStyle={{minWidth:'300px',width:'70%',marginBottom:'10px'}} height='150px' />
    <Counter  title={'Quantity'} overrideStyle={{minWidth:'150px',width:'30%',marginBottom:'10px',height:'150px'}} />
    </div>
    </div>
    <div className='d-flex justify-content-center' style={{width:'100%',maxWidth:'320px',minWidth:'250px',margin:'10px'}}>
      <DropArea />
    </div>
  </div>
      </div>
      
    </div>
  )
}
export default ListItems